@import "base";

* {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #333;
  color: #fff;
  padding: 1em;
  a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    letter-spacing: -0.05rem;
  }
  a:visited {
    color: #fff;
  }
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 1em;
}

.page-title { font-size: 2.5em; }

.tagline {
  font-style: italic;
  font-weight: 100;
}

.flavor-text {
  font-style: italic;
  font-weight: 100;
  font-size: 1.5em;
  padding: 1em;
  background-color: #eee;
}

.page-content {
  margin-top: 2em;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.layout-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.index-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-gap: 1em;
}

ul.unstyled {
  list-style: none;
  margin: 0;
  padding: 0;
}

hr { margin: 2em 0; }
dl {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 1em;
}
dt {
  font-weight: bold;
  grid-column: 1; /* Place in column 1 */
}
dd {
  margin: 0;
  grid-column: 2; /* Place in column 2 */
}

table {
  border-collapse: collapse;
  width: fit-content;
}

tbody tr:nth-child(odd) {
  background-color: #EEE; /* Alternate color for even rows */
}

th, td {
  padding: 0.5em;
  border: 1px solid #CCC;
}